.wrapper {
  display: flex;
  width: 100%;
  align-items: stretch;
}

#content {
  width: 100%;
  padding: 20px;
  min-height: 100vh;
  transition: all 0.3s;
}

#sidebar {
  min-width: 400px;
  max-width: 400px;
  transition: all 0.3s;
}


